/** @format */

.section {
    width: 100%;
    margin: 5px 10px;
    padding: 5px;
}

.wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
}

.textContainer {
    flex-basis: 250px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 5px;
    margin-bottom: 0px;
}

.itemContainer {
    flex-grow: 1;
    flex-basis: 250px;
    max-width: 300px;
    margin: 5px;
    margin-bottom: 0px;
}

/* For mobile phones: */
@media only screen and (max-width: 600px) {
    .wrapper {
        align-items: flex-start;
    }
}
